import { Component, OnInit, ViewChild } from '@angular/core';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES, YES_NO_OPTION } from '@shared';
import { MatTableDataSource } from '@angular/material/table';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Affiliation } from '@models';
import { FormListComponent } from '@fragments';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-affiliation',
  templateUrl: './affiliation.component.html',
  styleUrls: ['./affiliation.component.scss']
})
export class AffiliationComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<Affiliation>;
  displayedColumns: string[] = ['affiliationDesc', 'member', 'status'];
  statusList = [...STATUSES];
  yesNoList = [...YES_NO_OPTION];

  affiliation = new Affiliation();
  affiliationList = [];

  affiliationForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.affiliation).subscribe((res: any) => {
      this.affiliationList = res.responseData.data;
      this.dataSource = new MatTableDataSource<Affiliation>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    if (this.affiliation.member.toString() === 'No') {
      this.affiliation.member = this.setValue(this.affiliation.member);
    }
    this.affiliation.status = COMMON_FUNCTION.ACTIVE;
    this.apiService.save(ENDPOINTS.affiliation, this.affiliation).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  getAffiliation(): void {
    this.apiService.findById(ENDPOINTS.affiliation, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.affiliation = res.responseData.data;
      }
    });
  }

  confirmUpdate() {
    if (this.affiliation.status === 'INACTIVE') {
      Swal.fire({
        text: `Admin should manually change the company's membership affiliation value to its equivalent non member value. Continue?`,
        icon: 'question',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Confirm'
      }).then((result) => {
        if (result.value) {
          this.update();
        }
      });
    } else {
      this.update();
    }
  }

  update(): void {
    this.affiliation.member = this.setValue(this.affiliation.member);
    this.apiService.update(ENDPOINTS.affiliation, this.affiliation).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  setFormValidator(): void {
    this.affiliation.status = COMMON_FUNCTION.ACTIVE;
    this.affiliation.member = true;
    this.affiliationForm = this.formBuilder.group({
      description: ['', Validators.required],
      member: ['', Validators.required],
      status: [''],
    });
  }

  resetFormValidator(): void {
    this.affiliation = new Affiliation();
    this.affiliation.status = COMMON_FUNCTION.ACTIVE;
    this.affiliation.member = true;
    this.affiliationForm.markAsUntouched();
    this.affiliationForm.markAsPristine();
  }

  setValue(param): boolean {
    return param === 'Yes' || param === true ? true : false;
  }

  compareMember(value: any, selected: any): boolean {
    if (value) {
      return value === (selected ? 'Yes' : 'No');
    }
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this brand?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.affiliation, this.affiliation.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Affiliation successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
